/* Слайдер */
$(document).ready(function(){
  $('.certificate__slider').slick({
    lazyLoad: 'ondemand',
    slidesToShow: 4,
    slidesToScroll: 1,
    infinite: true,
    prevArrow:  `<button type="button" class="slick-prev"><img src='./img/svg/arrow-left.svg'></button>`,
    nextArrow:  `<button type="button" class="slick-next"><img src='./img/svg/arrow-right.svg'></button>`,
    responsive: [
      {
        breakpoint: 960,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: false
        }
      },
      {
          breakpoint: 768,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
        }
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
      }
    },
    ]
  });
});

